.DrawerToggleButton {
  width: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  padding: 10px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
}

.DrawerToggleButton div {
  width: 90%;
  height: 3px;
  margin-bottom: 5px;
  border-radius: 5px;
  background-color: #000;
}

@media (min-width: 500px) {
  .DrawerToggleButton {
    display: none;
  }
}



